import React, {Suspense, lazy} from 'react';
import './App.css';
import {Helmet} from 'react-helmet';

import {
  BrowserRouter as Router,
  Route,
  Redirect,
  Switch,
} from 'react-router-dom';

const RegionalSummary = lazy(() =>
  import('./component/RegionalSummary' )
);
const DailySummary = lazy(() =>
  import('./component/DailySummary')
);
const ContactSummary = lazy(() =>
  import('./component/ContactSummary')
);
const MedicalClgSummary = lazy(() =>
  import('./component/MedicalClgSummary')
);
const NotificationSummary = lazy(() =>
  import('./component/NotificationSummary')
);
const TestSummary = lazy(() =>
  import('./component/TestSummary')
);
const TravelHistorySummary = lazy(() =>
  import('./component/TravelHistorySummary')
);

const World = lazy(() => 
import('./component/World')
);

function App() {

  const pages = [
    {
      pageLink: '/',
      view: DailySummary,
      displayName: 'DailySummary',
      animationDelayForNavbar: 0.1,
      showInNavbar: true
    },
    {
      pageLink: '/regionalsummary',
      view: RegionalSummary,
      displayName: 'RegionalSummary',
      animationDelayForNavbar: 0.2,
      showInNavbar: true
    },
    {
      pageLink: '/contactsummary',
      view: ContactSummary,
      displayName: 'ContactSummary',
      animationDelayForNavbar: 0.3,
      showInNavbar: true
    },
    {
      pageLink: '/medicalclgsummary',
      view: MedicalClgSummary,
      displayName: 'MedicalClgSummary',
      animationDelayForNavbar: 0.4,
      showInNavbar: true
    },
    {
      pageLink: '/notificationsummary',
      view: NotificationSummary,
      displayName: 'NotificationSummary',
      animationDelayForNavbar: 0.5,
      showInNavbar: true
    },
    {
      pageLink: '/testsummary',
      view: TestSummary,
      displayName: 'TestSummary',
      animationDelayForNavbar: 0.6,
      showInNavbar: true
    },
    {
      pageLink: '/travelhistorysummary',
      view: TravelHistorySummary,
      displayName: 'TravelHistorySummary',
      animationDelayForNavbar: 0.7,
      showInNavbar: true
    },
    {
      pageLink: '/world',
      view: World,
      displayName: 'World',
      animationDelayForNavbar: 0.8,
      showInNavbar: true
    }
  ];
  const schemaMarkup = {
    '@context': 'http://schema.org/',
    '@type': 'NGO',
    name: 'Coronavirus Outbreak in India: Latest Map and Case Count',
    alternateName: 'COVID-19 Tracker',
    url: 'https://www.covid19india.org/',
    image: 'https://www.covid19india.org/thumbnail.png',
  };
  
  return (
    <div className="App">
      <Helmet>
        <script type="application/ld+json">
          {JSON.stringify(schemaMarkup)}
        </script>
      </Helmet>

    <Router>
        <Suspense fallback={<div className="lazy"></div>}>
          <Route
            render={({location}) => (
              <React.Fragment>
                <Switch location={location}>
                  {pages.map((page, index) => {
                    return (
                      <Route
                        exact
                        path={page.pageLink}
                        render={({match}) => (
                          <page.view key={match.params.stateCode || index} />
                        )}
                        key={index}
                      />
                    );
                  })}
                  <Redirect to="/" />
                </Switch>
              </React.Fragment>
            )}
          />
        </Suspense>
      </Router>
    </div>
  );
}

export default App;
